import { formatNumber } from '@cetus/utils'
import { HStack, Text, VStack } from '@chakra-ui/react'

export default function CustomPirceRangeTooltip({ payload, label }) {
  return (
    payload &&
    payload.length && (
      <VStack padding="16px" bg="bg_secondary" borderRadius="12px" border="1px solid border_secondary" justifyContent="flex-start" alignItems="left">
        <Text fontSize="12px">{payload[0].payload.tooltipTime}</Text>
        <HStack textAlign="left">
          <Text color="#68FFD8" fontSize="12px">
            Pool Price
          </Text>
          <Text color="#68FFD8" fontSize="12px">
            {formatNumber(payload[0].payload.real)}
          </Text>
        </HStack>
        <HStack>
          <Text color="#6FBCF0" fontSize="12px">
            Price Range
          </Text>
          <Text color="#6FBCF0" fontSize="12px">
            {formatNumber(payload[0].payload.lower)}-{formatNumber(payload[0].payload.upper)}
          </Text>
        </HStack>
      </VStack>
    )
  )
}
