import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { d, formatNumber } from '@cetus/utils'
import { useMemo, useState } from 'react'
import { Bar, ComposedChart, Legend, Line, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts'
import { CustomizedApyAndFeesRightYAxisTick, CustomizedApyAndFeesYAxisTick } from './CustomizedAxisTick'
import { ApyAndFeesChartTooltip } from './CustomTooltip'
type ApyAndFeesChartProps = {
  data: any
  dateType: string
  quoteCoin?: Token
}

export default function ApyAndFeesChart({ data = [], dateType, quoteCoin }: ApyAndFeesChartProps) {
  const [hidden, setHidden] = useState<{ apy: boolean; lp_fee: boolean }>({
    apy: false, // 控制 `Bar` 是否隐藏
    lp_fee: false // 控制 `Line` 是否隐藏
  })
  const handleLegendClick = (e: any) => {
    setHidden(prev => ({ ...prev, [e.id]: !prev[e.id] }))
  }
  const legendPayload = [
    { value: 'Cumulative Yields', type: 'line', id: 'lp_fee', color: '#07EBAD' }, // 对应折线图
    { value: 'APY', type: 'line', id: 'apy', color: '#75C8FF' } // 对应柱状图
  ]

  const ticks = useMemo(() => {
    if (data.length) {
      const min = Math.min(...data.map((item: any) => item.apy))
      const max = Math.max(...data.map((item: any) => item.apy))
      const tick4 = d(max).mul(1.05)
      const tick0 = d(min)
      const interval = tick4.sub(tick0).div(4)
      return Array.from({ length: 5 }, (_, i) => tick0.add(interval.mul(i)).toNumber())
    }
    return []
  }, [data])

  const ticksFee = useMemo(() => {
    if (data.length) {
      const min = Math.min(...data.map((item: any) => item.lp_fee))
      const max = Math.max(...data.map((item: any) => item.lp_fee))
      const tick4 = d(max).mul(1.05)
      const tick0 = d(min)
      const interval = tick4.sub(tick0).div(4)
      return Array.from({ length: 5 }, (_, i) => tick0.add(interval.mul(i)).toNumber())
    }
    return []
  }, [data])

  const getXData = (dataLength: number, value: any) => {
    const date = new Date(value * 1000)
    const hours = date.getUTCHours()
    const minutes = date.getUTCMinutes()
    const day = date.getUTCDate()
    const month = date.getUTCMonth() + 1

    if (dataLength < 24 || dateType === '24H') {
      const period = hours >= 12 ? 'PM' : 'AM'
      const displayHours = hours % 12 || 12
      return `${String(displayHours).padStart(2, '0')}:${String(minutes).padStart(2, '0')} ${period}`
    }
    return `${month}/${day}`
  }

  const { isApp } = useWindowWidth()

  return (
    <ResponsiveContainer width="100%" height="100%">
      <ComposedChart data={data} margin={{ top: 0, left: 10, right: 28, bottom: 5 }}>
        <XAxis
          dataKey="date"
          fontSize="12px"
          axisLine={false}
          tickLine={false}
          tick={{ fill: '#909CA4' }}
          dy={16}
          tickFormatter={value => getXData(data.length, value)}
          minTickGap={data?.length > 8 ? 36 : undefined}
          interval={data?.length <= 8 ? 0 : undefined}
        />
        <YAxis
          yAxisId="bar"
          xAxisId="x"
          fontSize="12px"
          orientation="left"
          width={50}
          tickLine={false}
          position="insideRight"
          ticks={ticks}
          domain={[ticks[0], ticks[ticks.length - 1]]}
          tickFormatter={value => {
            console.log('🚀 ~ ApyAndFeesChart 323~ value:', value)
            return formatNumber(value, 2) // 需要 return
          }}
          tick={<CustomizedApyAndFeesYAxisTick />}
        />
        <YAxis
          yAxisId="line"
          orientation="right"
          fontSize="12px"
          tickLine={false}
          ticks={ticksFee}
          domain={[ticksFee[0], ticksFee[ticksFee.length - 1]]}
          tick={<CustomizedApyAndFeesRightYAxisTick isRight={true} isApp={isApp} symbol={quoteCoin?.symbol} />}
        />
        <Tooltip content={<ApyAndFeesChartTooltip symbol={quoteCoin?.symbol} />} />
        <Legend
          align="right"
          verticalAlign="top"
          layout="horizontal"
          wrapperStyle={{ top: -32, right: 0, fontSize: 12 }}
          payload={legendPayload}
          // onClick={handleLegendClick}
        />
        <Bar yAxisId="bar" barSize={1} dataKey="apy" fill="#75C8FF" isAnimationActive={false} hide={hidden.apy} />

        {/* 折线图 */}
        <Line
          yAxisId="line"
          type="monotone"
          dataKey="lp_fee"
          stroke="#07EBAD"
          activeDot={{ r: 4, stroke: '#07EBAD' }}
          dot={false}
          isAnimationActive={false}
          hide={hidden.lp_fee}
        />
      </ComposedChart>
    </ResponsiveContainer>
  )
}
