import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d, formatNumber } from '@cetus/utils'
import { useMemo, useState } from 'react'
import { Legend, Line, LineChart, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts'
import CustomPirceRangeTooltip from './CustomPriceRangeTooltip'
import { CustomizedVaultYAxisTick } from './CustomizedAxisTick'

type PriceRangeChartProps = {
  data: any
  dateType: string
  isTabLoading: boolean
}

export default function PriceRangeChart({ data = [], dateType, isTabLoading }: PriceRangeChartProps) {
  const slicedData = useMemo(() => data, [data])
  const [hidden, setHidden] = useState({ lower: false, upper: false, real: false })

  const handleLegendClick = e => {
    setHidden(prev => ({ ...prev, [e.value]: !prev[e.value] }))
  }

  const ticks = useMemo(() => {
    if (slicedData.length) {
      const min = Math.min(...slicedData.map((item: any) => item.lower))
      const max = Math.max(...slicedData.map((item: any) => item.upper))
      const tick4 = d(max).mul(1.1)
      const tick0 = d(min).mul(0.9)
      const interval = tick4.sub(tick0).div(4)
      return Array.from({ length: 5 }, (_, i) => tick0.add(interval.mul(i)).toNumber())
    }
    return []
  }, [slicedData])

  const getXData = (dataLength: number, value: any) => {
    const date = new Date(value * 1000)
    const hours = date.getUTCHours()
    const minutes = date.getUTCMinutes()
    const day = date.getUTCDate()
    const month = date.getUTCMonth() + 1

    if (dataLength < 24 || dateType === '24H') {
      const period = hours >= 12 ? 'PM' : 'AM'
      const displayHours = hours % 12 || 12
      return `${String(displayHours).padStart(2, '0')}:${String(minutes).padStart(2, '0')} ${period}`
    }
    return `${month}/${day}`
  }

  const { isApp } = useWindowWidth()
  return (
    <ResponsiveContainer width="100%" height="100%">
      <LineChart data={slicedData} margin={{ top: 0, left: 10, right: 28, bottom: 5 }}>
        <XAxis
          dataKey="date"
          fontSize="12px"
          axisLine={false}
          tickLine={false}
          tick={{ fill: '#909CA4' }}
          dy={16}
          tickFormatter={value => (isTabLoading ? '' : getXData(slicedData.length, value))}
          minTickGap={slicedData?.length > 8 ? 36 : undefined}
          interval={slicedData?.length <= 8 ? 0 : undefined}
        />
        <YAxis
          fontSize="12px"
          ticks={ticks}
          domain={[ticks[0], ticks[ticks.length - 1]]}
          axisLine={false}
          tickLine={false}
          tickFormatter={value => formatNumber(value, 4)}
          tick={<CustomizedVaultYAxisTick />}
        />
        <Tooltip content={<CustomPirceRangeTooltip />} />
        <Legend
          align="right"
          verticalAlign="top"
          layout="horizontal"
          wrapperStyle={{ top: isApp ? -8 : -41, right: isApp ? 4 : 100, fontSize: 12 }}
          formatter={value => ({ lower: 'Price Range', upper: 'Price Range', real: 'Pool Price' })[value] || value}
        />
        <Line
          type="monotone"
          dataKey="real"
          stroke="#07EBAD"
          activeDot={{ r: 4, stroke: '#07EBAD' }}
          hide={hidden.real}
          dot={false}
          isAnimationActive={false}
        />
        <Line
          type="monotone"
          dataKey="lower"
          stroke="#6FBCF0"
          activeDot={{ r: 4, stroke: '#6FBCF0' }}
          dot={false}
          hide={hidden.lower}
          isAnimationActive={false}
        />
        <Line
          type="monotone"
          dataKey="upper"
          stroke="#6FBCF0"
          activeDot={{ r: 4, stroke: '#6FBCF0' }}
          legendType="none"
          dot={false}
          hide={hidden.upper}
          isAnimationActive={false}
        />
      </LineChart>
    </ResponsiveContainer>
  )
}
