import { Block } from '@cetus/design'
import SingleTokenInfo from '@cetus/design/src/components/common/SingleTokenInfo'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { HTextLabelBox } from '@cetus/ui-kit'
import VTextLabelBox from '@cetus/ui-kit/src/components/VTextLabelBox'
import { d, formatCurrency, formatNumber, formatNumberWithThreshold, textEllipses } from '@cetus/utils'
import { HStack, Stack, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'

export function MyLiquidity(props: {
  sharePoolRate?: string
  balanceInfo?: any
  totalValue?: string
  displayTokenA?: Token
  displayTokenB?: Token
  lpTokenInfo?: any
  holdCoinAmounts?: any
  isLoading?: boolean
  isReverse?: boolean
  holdCoinAValue?: string
  holdCoinBValue?: string
  rewardPerA?: string
  rewardPerB?: string
}) {
  const {
    holdCoinAmounts,
    lpTokenInfo,
    displayTokenA,
    displayTokenB,
    sharePoolRate,
    balanceInfo,
    // totalValue,
    isReverse,
    holdCoinAValue,
    holdCoinBValue,
    rewardPerA,
    rewardPerB
  } = props

  const displayValueA = useMemo(() => {
    return isReverse ? holdCoinBValue : holdCoinAValue
  }, [isReverse, holdCoinAValue, holdCoinBValue])

  const displayValueB = useMemo(() => {
    return isReverse ? holdCoinAValue : holdCoinBValue
  }, [isReverse, holdCoinAValue, holdCoinBValue])

  const totalValue = useMemo(() => {
    return d(displayValueA).add(d(displayValueB))
  }, [displayValueA, displayValueB, displayTokenA, displayTokenB])

  const { isApp } = useWindowWidth()

  return (
    <Block bg="bg_six" borderColor="border_secondary" p={{ base: '20px 8px', lg: '20px 16px' }}>
      <VStack w="100%" gap={{ base: '24px', lg: '8px' }}>
        <HStack w="100%" justifyContent="space-between">
          <HStack>
            <Text textAlign="start" fontSize="16px" color="text_caption">
              Your Liquidity
            </Text>
            {/** 由于当前数据较低，先隐藏icon */}
            {/* <CetusTooltip
              tooltip={
                <VStack gap="8px" cursor="pointer" alignItems="start">
                  <Text fontSize="12px" lineHeight="20px">
                    Your estimated daily earnings to be compounded according to recent vault performance.
                  </Text>
                  {rewardPerA && rewardPerB && (
                    <Text fontSize="14px" color="text_caption">
                      {`${rewardPerA} + ${rewardPerB}`}
                    </Text>
                  )}
                </VStack>
              }
            >
              <Image src="/images/icon_apr@2x.png" w="24px" h="24px" />
            </CetusTooltip> */}
          </HStack>

          {/* 持仓占比 */}
          <HStack gap="8px">
            <Text whiteSpace="nowrap">Your share of pool</Text>
            <Text color="text_highlight" fontWeight="500">
              {formatNumberWithThreshold(sharePoolRate, 2, 6)}%
            </Text>
          </HStack>
        </HStack>
        {isApp ? (
          <VStack w="100%" gap="16px">
            <HTextLabelBox
              label="LP Token Balance"
              labelStyle={{ fontSize: '13px' }}
              value={
                <HStack>
                  <SingleTokenInfo
                    token={lpTokenInfo}
                    haveName={false}
                    haveSymbol={false}
                    symbolFontSize="12px"
                    warningIcon={{ isNeedShow: false }}
                    imgBoxStyle={{ w: '28px', h: '28px' }}
                    haveTooltip={true}
                  />
                  <Text color="text_caption" ml="4px" fontWeight="500" fontSize="13px">
                    {balanceInfo?.balanceFormat ? formatNumber(balanceInfo?.balanceFormat, 9) : '-'} LP
                  </Text>
                </HStack>
              }
              subValue={<Text>{balanceInfo?.balanceFormat && totalValue ? formatCurrency(totalValue, 2) : '-'}</Text>}
              subValueStyle={{ fontSize: '13px' }}
            />
            <HTextLabelBox
              label={textEllipses(displayTokenA?.symbol, 10)}
              labelStyle={{ fontSize: '13px' }}
              value={
                <HStack>
                  <SingleTokenInfo
                    token={displayTokenA}
                    haveName={false}
                    haveSymbol={false}
                    symbolFontSize="12px"
                    warningIcon={{ iconW: '14px', iconH: '14px' }}
                    imgBoxStyle={{ w: '20px', h: '20px' }}
                    haveTooltip={true}
                  />
                  <Text
                    fontSize="13px"
                    color="text_caption"
                    fontWeight="500"
                  >{`${formatNumber(holdCoinAmounts?.displayCoinA, 6)} ${textEllipses(displayTokenA?.symbol, 10)}`}</Text>
                </HStack>
              }
              subValue={formatCurrency(displayValueA, 2)}
              subValueStyle={{ fontSize: '13px' }}
            />
            <HTextLabelBox
              label={textEllipses(displayTokenB?.symbol, 10)}
              labelStyle={{ fontSize: '13px' }}
              value={
                <HStack>
                  <SingleTokenInfo
                    token={displayTokenB}
                    haveName={false}
                    haveSymbol={false}
                    symbolFontSize="12px"
                    warningIcon={{ iconW: '14px', iconH: '14px' }}
                    imgBoxStyle={{ w: '20px', h: '20px' }}
                    haveTooltip={true}
                  />

                  <Text
                    fontSize="13px"
                    color="text_caption"
                    fontWeight="500"
                  >{`${formatNumber(holdCoinAmounts?.displayCoinB, 6)} ${textEllipses(displayTokenB?.symbol, 10)}`}</Text>
                </HStack>
              }
              subValue={formatCurrency(displayValueB, 2)}
              subValueStyle={{ fontSize: '13px' }}
            />
          </VStack>
        ) : (
          <>
            {/* 余额 */}
            <Stack flexDir={{ base: 'column', lg: 'row' }} mt="16px" gap={{ base: '8px', lg: '4px' }} align="center">
              <Text>LP Token Balance</Text>
              <Text color="text_caption" ml="4px" fontWeight="500">
                {balanceInfo?.balanceFormat ? formatNumber(balanceInfo?.balanceFormat, 9) : '-'} LP
              </Text>
              <Text>{balanceInfo?.balanceFormat ? formatCurrency(totalValue, 2) : '-'}</Text>
            </Stack>

            {/* token详情 */}
            <HStack
              w={{ base: '100%', lg: '90%' }}
              mt="12px"
              justifyContent="space-between"
              pos="relative"
              // bg="linear-gradient(270deg, #0F0F0F 0%, #252525 50%, #0F0F0F 100%)"
              background="url(/images/vault_your_liquidity_bg@2x.png)"
              backgroundSize="100% auto"
              borderRadius="69px"
              gap="0"
            >
              {/* 左边 */}
              <VStack flex="1" h="120px" justifyContent="center" alignItems="start">
                <HStack>
                  <SingleTokenInfo
                    token={displayTokenA}
                    haveName={false}
                    haveSymbol={false}
                    symbolFontSize="12px"
                    warningIcon={{ iconW: '14px', iconH: '14px' }}
                    imgBoxStyle={{ w: '28px', h: '28px' }}
                    haveTooltip={true}
                  />
                  <Text color="text_caption" fontSize="16px" fontWeight="600">
                    {textEllipses(displayTokenA?.symbol, 10)}
                  </Text>
                </HStack>
                <VTextLabelBox
                  wrapStyle={{
                    gap: '4px',
                    w: 'calc(100% - 60px)'
                  }}
                  title={`${formatNumber(holdCoinAmounts?.displayCoinA, 6)} ${textEllipses(displayTokenA?.symbol, 10)}`}
                  titleStyle={{
                    fontSize: { base: '12px', lg: '14px' },
                    color: 'text_caption',
                    whiteSpace: 'wrap',
                    lineHeight: { base: '16px', lg: '14px' },
                    wordBreak: 'break-all',
                    fontWeight: 500
                  }}
                  value={formatCurrency(displayValueA, 2)}
                  valueStyle={{
                    fontSize: { base: '12px', lg: '14px' },
                    color: 'text_paragraph',
                    whiteSpace: 'wrap',
                    lineHeight: { base: '16px', lg: '14px' },
                    wordBreak: 'break-all',
                    fontWeight: 500
                  }}
                />
              </VStack>
              {/* 右边 */}
              <VStack flex="1" h="120px" justifyContent="center" alignItems="end">
                <HStack>
                  <Text color="text_caption" fontSize="16px" fontWeight="600">
                    {textEllipses(displayTokenB?.symbol, 10)}
                  </Text>
                  <SingleTokenInfo
                    token={displayTokenB}
                    haveName={false}
                    haveSymbol={false}
                    symbolFontSize="12px"
                    warningIcon={{ iconW: '14px', iconH: '14px' }}
                    imgBoxStyle={{ w: '28px', h: '28px' }}
                    haveTooltip={true}
                  />
                </HStack>

                <VTextLabelBox
                  wrapStyle={{
                    gap: '4px',
                    alignItems: 'flex-end',
                    w: 'calc(100% - 60px)'
                  }}
                  title={`${formatNumber(holdCoinAmounts?.displayCoinB, 6)} ${textEllipses(displayTokenB?.symbol, 10)}`}
                  titleStyle={{
                    fontSize: { base: '12px', lg: '14px' },
                    color: 'text_caption',
                    whiteSpace: 'wrap',
                    lineHeight: { base: '16px', lg: '14px' },
                    wordBreak: 'break-all',
                    fontWeight: 500
                  }}
                  value={formatCurrency(displayValueB, 2)}
                  valueStyle={{
                    fontSize: { base: '12px', lg: '14px' },
                    color: 'text_paragraph',
                    whiteSpace: 'wrap',
                    lineHeight: { base: '16px', lg: '14px' },
                    wordBreak: 'break-all',
                    fontWeight: 500
                  }}
                />
              </VStack>
              {/* 中间icon */}
              <Block
                pos="absolute"
                left="calc(50% - 52px)"
                w="104px"
                h="104px"
                borderRadius="70px"
                p="8px 0px"
                // display="flex"
                // flexDirection="column"
                // alignItems="center"
                // justifyContent="center"
              >
                <VStack justify="center" gap="4px">
                  <SingleTokenInfo
                    token={lpTokenInfo}
                    haveName={false}
                    haveSymbol={false}
                    symbolFontSize="12px"
                    warningIcon={{ isNeedShow: false }}
                    imgBoxStyle={{ w: '58px', h: '58px' }}
                    haveTooltip={true}
                  />
                  <Text color="#8BAABE" fontSize="12px">
                    Pooled Assets
                  </Text>
                </VStack>
              </Block>
            </HStack>
          </>
        )}
      </VStack>
    </Block>
  )
}
