import VaultsAddConfirmModel from '@/components/modal/VaultsAddConfirmModel'
import vaultInterface from '@/hooks/vault-interface'
import { useCalculateChangeLpRate, useCalculateSlippageAmount } from '@/hooks/vaults/useVaultsHelper'
import useGlobalStore from '@/store/common/global'
import { AssetActionType, VaultApiInfo, VaultsAddModelData } from '@/types/vaults'
import { Block, TradeInputGroup } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores/src/useAccountStore'
import { Token } from '@cetus/types'
import { Button, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import { useParams } from 'react-router-dom'
import { v4 } from 'uuid'
import AvailableCapacity from './AvailableCapacity'
import { SwitchAssetTab } from './SwitchAssetTab'
import { VaultsAddResult } from './VaultsAddResult'

type VaultsAddProps = {
  apiVaultInfo?: VaultApiInfo
  tokenA?: Token
  tokenB?: Token
  isReverse: boolean
  vaultsId?: string
  totalLp?: string
  feeTier?: string
  isUnstableVault: boolean
  handleRefreshClick: () => void
  currentActionTab?: string
  hardCapUSD?: string
  vaultTvl?: string
  lpTokenInfo?: Token
  depositRatio?: string
  gtAumLimit?: boolean
  quoteCoin?: Token
  maxCapNum?: string
  isRefresh?: boolean
}

export function VaultsAdd(props: VaultsAddProps) {
  const { category } = useParams()
  const {
    apiVaultInfo,
    tokenA,
    tokenB,
    isReverse,
    vaultsId,
    totalLp,
    feeTier,
    handleRefreshClick,
    currentActionTab,
    hardCapUSD,
    vaultTvl,
    lpTokenInfo,
    depositRatio,
    gtAumLimit,
    quoteCoin,
    maxCapNum,
    isRefresh
  } = props
  const useVaultsAdd = vaultInterface.useVaultsAdd(category == 'haedal')
  const useAddButtonStatus = vaultInterface.useAddButtonStatus(category == 'haedal')
  const [assetAction, setAssetAction] = useState<AssetActionType>('both')
  const [isCheckedZAP, setIsCheckedZAP] = useState(false)
  const { currentAccount, onWalletModal } = useAccountStore()
  const {
    amountInputA,
    amountInputB,
    balanceInfoA,
    balanceInfoB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    fixAmountA,
    inputTotalValue,
    handleAmountInputChange,
    calculateResult,
    showOnlySideTips,
    doAddAction,
    reCalculateResult,
    overHardtop,
    zapNumError,
    calculateLpLoading,
    inputTotalValueError,
    debouncedPreCalculateDepositValue,
    percentage,
    setPercentage,
    setIsSlider,
    isSlider,
    setUuid
  } = useVaultsAdd(
    assetAction,
    handleRefreshClick,
    vaultsId,
    isReverse ? tokenB : tokenA,
    isReverse ? tokenA : tokenB,
    apiVaultInfo?.isReverse,
    isCheckedZAP,
    hardCapUSD,
    vaultTvl,
    gtAumLimit
  )

  const { vaultsSlippage } = useGlobalStore()

  const { btnText, btnDisabled } = useAddButtonStatus(
    isCheckedZAP,
    assetAction,
    true,
    amountInputA,
    amountInputB,
    fixAmountA,
    balanceInfoA?.balanceFormat,
    balanceInfoB?.balanceFormat,
    tokenA,
    tokenB,
    zapNumError,
    calculateResult,
    overHardtop,
    inputTotalValueError
  )

  // 占比
  const { lpRate } = useCalculateChangeLpRate(calculateResult?.ft_amount, totalLp)
  // 最小可接收
  const { amountLimit } = useCalculateSlippageAmount(Number(vaultsSlippage), calculateResult?.ft_amount, false)

  const [isOpenAddModel, setIsOpenAddModel] = useState<boolean>(false)

  const balanceA = useMemo(() => {
    return balanceInfoA
  }, [balanceInfoA?.balance, balanceInfoB?.balance, isReverse])

  const balanceB = useMemo(() => {
    return balanceInfoB
  }, [balanceInfoA?.balance, balanceInfoB?.balance, isReverse])

  const displayTokenA = useMemo(() => {
    return tokenA
  }, [isReverse, tokenA, tokenB])

  const displayTokenB = useMemo(() => {
    return tokenB
  }, [isReverse, tokenA, tokenB])

  const getSecondModelData = () => {
    const data: VaultsAddModelData = {
      feeTier: feeTier || '',
      displayTokenA: displayTokenA!,
      displayTokenB: displayTokenB!,
      displayAmountA: amountInputA,
      displayAmountB: amountInputB,
      totalAmountValue: inputTotalValue,
      sharePool: lpRate,
      lpAmountLimit: amountLimit || '0',
      lpDecimals: lpTokenInfo?.decimals
    }

    return data
  }

  // useEffect(() => {
  //   reCalculateResult()
  // }, [apiVaultInfo])

  const { isApp } = useWindowWidth()

  useEffect(() => {
    if (isSlider) {
      const uuid = v4()
      setUuid(uuid)
      debouncedPreCalculateDepositValue(percentage, uuid)
    }
  }, [isRefresh])

  return (
    <VStack gap="12px" w="100%" align="flex-start">
      <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p={{ base: '16px 8px 8px', lg: '16px' }} w="100%">
        <SwitchAssetTab
          showAfterIcon={true}
          displayTokenB={isReverse ? tokenB : tokenB}
          displayTokenA={isReverse ? tokenA : tokenA}
          onSelectAssetTab={setAssetAction}
          assetAction={assetAction}
          isCheckedZAP={isCheckedZAP}
          setIsCheckedZAP={setIsCheckedZAP}
          setAssetAction={setAssetAction}
          isUnstableVault={category == 'haedal'}
          currentActionTab={currentActionTab}
          handleRefreshClick={() => {
            handleRefreshClick()
            reCalculateResult()
          }}
        />
        <VStack mt="16px" position="relative">
          <TradeInputGroup
            borderRadius="12px"
            from={{
              wrapStyle: {
                borderRadius: '12px',
                h: 'unset',
                p: { base: '20px 8px', lg: '20px 16px' }
              },
              hideSelf: assetAction !== 'both' && assetAction !== displayTokenA?.coin_type,
              balance: balanceA?.balanceFormat || '',
              value: amountInputA,
              amountValue: amountInputA && amountValueA,
              loading: (isReverse ? fixAmountA : !fixAmountA) && preCalculateLoading && !isCheckedZAP,
              onChange: value => {
                console.log('🚀🚀🚀 ~ VaultsAdd.tsx:183 ~ VaultsAdd ~ value,amountInputB:', value, amountInputB)
                handleAmountInputChange(value, !isReverse, true, amountInputB)
              },
              selectable: false,
              placeholder: '0.0',
              token: displayTokenA
            }}
            to={{
              wrapStyle: {
                borderRadius: '12px',
                h: 'unset',
                p: { base: '20px 8px', lg: '20px 16px' }
              },
              hideSelf: assetAction !== 'both' && assetAction !== displayTokenB?.coin_type,
              balance: balanceB?.balanceFormat || '',
              value: amountInputB,
              amountValue: amountInputB && amountValueB,
              loading: (isReverse ? !fixAmountA : fixAmountA) && preCalculateLoading && !isCheckedZAP,
              onChange: value => {
                handleAmountInputChange(value, isReverse, true, amountInputA)
              },
              selectable: false,
              placeholder: '0.0',
              token: displayTokenB
            }}
            iconParams={{
              xlinkHref: '#icon-icon_add',
              svgFill: 'text_caption',
              iconCursor: 'default'
            }}
            iconHover={false}
          />
          {/* {[
            fromTokenLock && (assetAction == displayTokenA?.coin_type || assetAction == 'both'),
            toTokenLock && (assetAction == displayTokenB?.coin_type || assetAction == 'both')
          ].filter(Boolean)?.length === 1 && (
            <LockInput
              position="absolute"
              top={assetAction == 'both' ? (fromTokenLock ? '0px' : '116px') : '0px'}
              h="108px"
              borderRadius="12px"
              left="0"
              text=""
            />
          )} */}
        </VStack>
      </Block>
      {apiVaultInfo?.category == 'haedal' && (
        <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p="16px">
          <AvailableCapacity
            depositRatio={depositRatio}
            percentage={percentage}
            hardCapUSD={hardCapUSD}
            vaultTvl={vaultTvl}
            gtAumLimit={gtAumLimit}
            quoteCoin={quoteCoin}
            maxCapNum={maxCapNum}
            onChange={value => {
              setIsSlider(true)
              setPercentage(value)
              debouncedPreCalculateDepositValue(value)
            }}
          />
        </Block>
      )}

      <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p="0px">
        <VStack w="100%" gap="20px">
          <Button
            m="-1px"
            isLoading={preCalculateLoading || isOpenAddModel}
            w="100%"
            borderRadius="12px"
            h="52px"
            fontSize={isApp && zapNumError ? '14px' : '18px'}
            fontWeight="500"
            disabled={btnDisabled || preCalculateLoading || isOpenAddModel || (currentAccount?.address && (gtAumLimit || overHardtop))}
            onClick={() => {
              if (currentAccount) {
                setIsOpenAddModel(true)
              } else {
                onWalletModal(true)
              }
            }}
          >
            {btnText || (isCheckedZAP ? 'Zap In' : 'Deposit')}
          </Button>

          {zapNumError && percentage > 0 && (amountInputA || amountInputB) ? (
            <VStack w="100%" pl="16px" pr="16px" pb={calculateResult || showOnlySideTips ? '0px' : '20px'} alignItems="start" gap="20px">
              <Text
                color="primary_yellow"
                fontSize="12px"
                textAlign="left"
                w="100%"
                bg="primary_yellow_opacity.10"
                p="12px"
                borderRadius="8px"
                lineHeight="20px"
              >
                Single-asset deposit can't be higher than $10,000 at a time.
              </Text>
            </VStack>
          ) : null}

          {inputTotalValueError && (
            <VStack w="100%" pl="16px" pb="20px" pr="16px" alignItems="start">
              <Text
                color="primary_red"
                fontSize="12px"
                textAlign="left"
                w="100%"
                bg="primary_red_opacity.10"
                p="12px"
                borderRadius="8px"
                lineHeight="20px"
              >
                The input is too small. Zap mode is not available.
              </Text>
            </VStack>
          )}

          {/* 展示计算结果 */}
          {(calculateResult || showOnlySideTips) && !inputTotalValueError && (
            <VStack w="100%" pb="20px" pl="16px" pr="16px" alignItems="start" gap="0">
              {assetAction !== 'both' && showOnlySideTips && (
                <Text color="text_paragraph" fontSize="12px" lineHeight="1.8" pb={calculateResult ? '20px' : '0'}>
                  {showOnlySideTips}
                </Text>
              )}
              {calculateResult && (
                <VaultsAddResult
                  amountLimit={amountLimit}
                  inputTotalValue={inputTotalValue}
                  preCalculateLoading={preCalculateLoading}
                  calculateLpLoading={calculateLpLoading}
                  lpDecimals={lpTokenInfo?.decimals}
                  lpRate={lpRate}
                  poolName={`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
                />
              )}
            </VStack>
          )}
        </VStack>
      </Block>

      {isOpenAddModel && (
        <VaultsAddConfirmModel
          data={getSecondModelData()}
          isOpen={isOpenAddModel}
          calculateLpLoading={calculateLpLoading}
          onClose={() => {
            setIsOpenAddModel(false)
          }}
          onSubmitClick={() => {
            setIsOpenAddModel(false)
            doAddAction()
          }}
        />
      )}
    </VStack>
  )
}
