import { useGetSuiStakeProtocol } from '@/hooks/vaults/useVaultsHelper'
import { AssetActionType } from '@/types/vaults'
import { SelectTab } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import { Token } from '@cetus/types'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import ZapSwitch from '../modal/common/ZapSwitch'

const hoverCoinTips =
  'You can also choose to deposit in only one asset type. In that case,the vault will convert your asset to the required propositions using Cetus.Please note that trading fees & slippage will be applied. When slippage is involved,a certain amount of post-swap dust (no loss of funds) will be returned to you.'

const hoverSuiTips = (stakeProtocolName?: string, symbol?: string) => {
  return `When you deposit in only SUI tokens, a part of your SUI amount will be converted to ${symbol} via ${stakeProtocolName} liquid staking services to match required liquidity compositions of the liquidity pool on Cetus. A certain amount of dust (no loss of funds) will be returned to you if slippage is involved.`
}

export function SwitchAssetTab(props: {
  displayTokenA?: Token
  displayTokenB?: Token
  onSelectAssetTab: (assetTab: AssetActionType) => void
  showAfterIcon?: boolean
  label?: string
  handleRefreshClick: () => void
  refreshCount: number
  setAssetAction: (value: string) => void
  assetAction: string
  isCheckedZAP: boolean
  setIsCheckedZAP: (value: boolean) => void
  isUnstableVault?: boolean
  currentActionTab?: string
}) {
  const {
    displayTokenA,
    displayTokenB,
    onSelectAssetTab,
    showAfterIcon = false,
    label = 'Deposit Amounts',
    handleRefreshClick,
    refreshCount,
    isCheckedZAP,
    setIsCheckedZAP,
    assetAction,
    setAssetAction,
    isUnstableVault,
    currentActionTab
  } = props

  const { stakeProtocolName } = useGetSuiStakeProtocol(displayTokenA?.coin_type)
  const [currentActionToken, setCurrentActionToken] = useState('both')

  const tabList = useMemo(() => {
    if (displayTokenA && displayTokenB) {
      const list: Tab<{ action: AssetActionType }>[] = [
        {
          label: `${displayTokenA.symbol} only`,
          action: displayTokenA.coin_type,
          afterIcon: isCheckedZAP && currentActionTab == 'Deposit' && !isUnstableVault ? { xlinkHref: '#icon-icon_tips' } : undefined,
          tooltip: isUnstableVault
            ? currentActionTab == 'Deposit'
              ? `You can add liquidity in this pool with only ${displayTokenA.symbol}.`
              : ''
            : hoverCoinTips
        },
        // {
        //   label: `${displayTokenA.symbol}+${displayTokenB.symbol}`,
        //   action: 'both'
        // },
        {
          label: `${displayTokenB.symbol} only`,
          action: displayTokenB.coin_type,
          afterIcon: isCheckedZAP && currentActionTab == 'Deposit' && !isUnstableVault ? { xlinkHref: '#icon-icon_tips' } : undefined,
          tooltip: isUnstableVault
            ? currentActionTab == 'Deposit'
              ? `You can add liquidity in this pool with only ${displayTokenB.symbol}.`
              : ''
            : hoverSuiTips(stakeProtocolName, displayTokenA?.symbol)
        }
      ]

      if (isUnstableVault && currentActionTab == 'Deposit') {
        list.splice(1, 0, {
          label: `${displayTokenA.symbol}+${displayTokenB.symbol}`,
          action: 'both',
          afterIcon: isCheckedZAP && !isUnstableVault && currentActionTab == 'Deposit' ? { xlinkHref: '#icon-icon_tips' } : undefined,
          tooltip: currentActionTab == 'Deposit' ? `You can add any value you want from both assets.` : ''
        })
      }

      // if (label == 'Remove Amounts' && isCheckedZAP) {
      //   list.splice(1, 1)
      // }

      return list
    }

    return []
  }, [displayTokenA, stakeProtocolName, isCheckedZAP])

  const currentTab = useMemo(() => {
    if (tabList.length === 0) {
      return undefined
    }
    if (isUnstableVault) {
      if (assetAction === displayTokenA?.coin_type) {
        return tabList[0].label
      }

      if (assetAction === displayTokenB?.coin_type) {
        return currentActionTab == 'Withdraw' ? tabList[1].label : tabList[2].label
      }
    } else {
      if (assetAction === displayTokenA?.coin_type) {
        return tabList[0].label
      }

      if (assetAction === displayTokenB?.coin_type) {
        return tabList[1].label
      }
    }

    return tabList[1].label
  }, [assetAction, tabList, isCheckedZAP])

  const tooltipType = useMemo(() => {
    if (currentActionTab == 'Deposit') {
      return isUnstableVault ? 'customDeposit' : 'deposit'
    }
    return 'withdraw'
  }, [currentActionTab, isUnstableVault])

  return (
    <VStack w="100%" gap="16px">
      <HStack w="100%" justifyContent="space-between">
        <Text fontWeight="500">{label}</Text>
        <ZapSwitch
          isCheckedZAP={isCheckedZAP}
          zapText={currentActionTab == 'Deposit' ? 'Zap In' : 'Zap Out'}
          tooltipType={tooltipType}
          zapSwitchChange={status => {
            setIsCheckedZAP(!isCheckedZAP)
            if (!status) {
              setAssetAction('both')
            } else {
              setAssetAction(displayTokenA?.coin_type)
            }
            setCurrentActionToken(displayTokenA?.symbol + ' only')
          }}
        />
      </HStack>
      {isCheckedZAP ? (
        <SelectTab
          wrapStyle={{
            w: '100%',
            h: '48px',
            borderRadius: '8px',
            p: '3px'
          }}
          itemStyle={{
            w: '50%',
            fontSize: '14px',
            borderRadius: '6px',
            fontWeight: '500'
          }}
          type="outlineTab"
          currentTab={currentTab}
          tabList={tabList}
          handleChangeTab={(item: Tab<{ action: AssetActionType }>) => {
            setAssetAction(item.action)
            console.log('🚀🚀🚀 ~ item.action:', item.action)
            onSelectAssetTab(item.action)
          }}
        />
      ) : null}
    </VStack>
  )
}
