import { VaultApiInfo } from '@/types/vaults'
import { AddressCopyLink, Block, CetusTooltip, TooltipIcon } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { CoinPairImage, HTextLabelBox, VTextLabelBox } from '@cetus/ui-kit'
import { symbolDataDisplayProcessing } from '@cetus/utils'
import { Center, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import { CoinImage, VaultsAprTooltipContent } from '../list/common/VaultsAprBlock'
import PoweredByHaedal from '../modal/common/PoweredByHaedal'

type VaultsHeaderProps = {
  apiVaultInfo?: VaultApiInfo
  isUnstableVault?: boolean
  vaultTvl: string
  performanceFee: string
}

export function VaultsHeader(props: VaultsHeaderProps) {
  const { isApp } = useWindowWidth()
  const { apiVaultInfo, isUnstableVault, vaultTvl, performanceFee } = props
  const { getExplorerUrl } = useExplorer()

  return (
    <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" justifyContent="space-between">
      {/* 左边区域 */}
      <HStack>
        <CoinPairImage
          coinACoinType={apiVaultInfo?.displayTokenA?.coin_type}
          coinBCoinType={apiVaultInfo?.displayTokenB?.coin_type}
          coinAIconUrl={apiVaultInfo?.displayTokenA?.logo_url}
          coinBIconUrl={apiVaultInfo?.displayTokenB?.logo_url}
        />

        <VStack alignItems="start" flexDirection={isUnstableVault ? 'column' : 'row'} gap="2px">
          <HStack>
            <Text fontSize="16px" color="text_caption">
              {apiVaultInfo?.name || `${apiVaultInfo?.displayTokenA?.symbol} - ${apiVaultInfo?.displayTokenB?.symbol}`}
            </Text>
            <Block w="unset" p="4px 6px" borderRadius="12px" ml="4px">
              <HStack>
                <Text fontSize="12px" color="primary">
                  {apiVaultInfo?.feeDisplay}
                </Text>
              </HStack>
            </Block>
          </HStack>
          <HStack mt="4px">{isUnstableVault && <PoweredByHaedal fontSize="14px" imgW="20px" imgH="16px" category="Haedal" />}</HStack>
        </VStack>
        {!isApp && (
          <HStack ml="12px" gap="4px">
            <Text fontSize="14px">Vault Address</Text>
            {/* 拷贝和跳转 */}
            <AddressCopyLink
              fontSize="14px"
              address={apiVaultInfo?.vaultsId || ''}
              onClickLink={() => {
                window.open(getExplorerUrl(apiVaultInfo?.vaultsId, 'poolAddress'), '_blank')
              }}
            />
          </HStack>
        )}
      </HStack>
      {/* 右边区域 */}
      <Stack flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '8px', lg: '60px' }}>
        {isApp ? (
          <>
            <HTextLabelBox
              label="Vault Address"
              labelStyle={{
                fontSize: '14px'
              }}
              value={
                <AddressCopyLink
                  fontSize="14px"
                  color="text_caption"
                  address={apiVaultInfo?.vaultsId || ''}
                  onClickLink={() => {
                    window.open(getExplorerUrl(apiVaultInfo?.vaultsId, 'poolAddress'), '_blank')
                  }}
                />
              }
            />
            <HTextLabelBox
              wrapStyle={{
                gap: '6px'
              }}
              label="APY"
              labelStyle={{
                fontSize: '14px'
              }}
              rightValueTip={
                <VaultsAprTooltipContent
                  vaultsLstApyDisplay={apiVaultInfo?.vaultsLstApyDisplay}
                  vaultsApyDisplay={apiVaultInfo?.vaultsApyDisplay}
                  vaultsTotalApyDisplay={apiVaultInfo?.vaultsTotalApyDisplay}
                  vaultsAprDisplay={apiVaultInfo?.vaultsAprDisplay}
                  category={apiVaultInfo?.category}
                />
              }
              value={apiVaultInfo?.category == 'cetus' ? apiVaultInfo?.vaultsTotalApyDisplay : apiVaultInfo?.vaultsApyDisplay || '-'}
              valueStyle={{
                color: 'text_highlight',
                h: '20px',
                lineHeight: '20px',
                fontSize: '14px',
                fontWeight: '500'
              }}
            />
            <HTextLabelBox
              wrapStyle={{
                gap: '6px'
              }}
              label="Earnings"
              labelStyle={{
                fontSize: '14px'
              }}
              value={
                <CetusTooltip
                  placement="top"
                  tooltip={
                    <Text fontSize="12px" lineHeight="20px" maxW="240px">
                      Rewards will be auto harvested and compounded to your position.
                    </Text>
                  }
                >
                  <Center>
                    <HStack w="100px" justifyContent="end">
                      {apiVaultInfo?.vaultsRewards.map(coinType => {
                        return <CoinImage key={coinType} coinType={coinType} />
                      })}
                    </HStack>
                  </Center>
                </CetusTooltip>
              }
              valueStyle={{
                h: '20px',
                lineHeight: '20px'
              }}
            />
            <HTextLabelBox
              wrapStyle={{
                gap: '6px'
              }}
              label="TVL"
              labelStyle={{
                fontSize: '14px'
              }}
              value={(isUnstableVault ? symbolDataDisplayProcessing(vaultTvl, '$') : apiVaultInfo?.vaultsTvlDisplay) || '-'}
              valueStyle={{
                color: 'text_caption',
                h: '20px',
                lineHeight: '20px',
                fontSize: '14px',
                fontWeight: '500'
              }}
            />
            {isUnstableVault && (
              <HTextLabelBox
                wrapStyle={{
                  gap: '6px'
                }}
                label={
                  <HStack>
                    <Text>Performance Fee</Text>
                    <TooltipIcon type="vaultDetailFee" />
                  </HStack>
                }
                labelStyle={{
                  fontSize: '14px'
                }}
                value={symbolDataDisplayProcessing(performanceFee, '%') || '-'}
                valueStyle={{
                  color: 'text_caption',
                  h: '20px',
                  lineHeight: '20px',
                  fontSize: '14px',
                  fontWeight: '500'
                }}
              />
            )}
          </>
        ) : (
          <>
            <VTextLabelBox
              wrapStyle={{
                alignItems: 'flex-end',
                gap: '6px'
              }}
              title="APY"
              titleStyle={{
                fontSize: '12px'
              }}
              valueTooltip={
                <VaultsAprTooltipContent
                  vaultsLstApyDisplay={apiVaultInfo?.vaultsLstApyDisplay}
                  vaultsApyDisplay={apiVaultInfo?.vaultsApyDisplay}
                  vaultsTotalApyDisplay={apiVaultInfo?.vaultsTotalApyDisplay}
                  vaultsAprDisplay={apiVaultInfo?.vaultsAprDisplay}
                  category={apiVaultInfo?.category}
                />
              }
              value={apiVaultInfo?.category == 'cetus' ? apiVaultInfo?.vaultsTotalApyDisplay : apiVaultInfo?.vaultsApyDisplay || '-'}
              valueStyle={{
                color: 'text_highlight',
                h: '20px',
                lineHeight: '20px',
                fontWeight: '500'
              }}
            />
            <VTextLabelBox
              wrapStyle={{
                gap: '6px',
                alignItems: 'flex-end'
              }}
              title="Earnings"
              titleStyle={{
                fontSize: '12px'
              }}
              value={
                <CetusTooltip
                  placement="top"
                  tooltip={
                    <Text fontSize="12px" lineHeight="20px" maxW="240px">
                      Rewards will be auto harvested and compounded to your position.
                    </Text>
                  }
                >
                  <Center>
                    <HStack justifyContent="end">
                      {apiVaultInfo?.vaultsRewards.map(coinType => {
                        return <CoinImage key={coinType} coinType={coinType} />
                      })}
                    </HStack>
                  </Center>
                </CetusTooltip>
              }
              valueStyle={{
                h: '20px'
              }}
            />
            <VTextLabelBox
              wrapStyle={{
                alignItems: 'flex-end',
                gap: '6px'
              }}
              title="TVL"
              titleStyle={{
                fontSize: '12px'
              }}
              value={(isUnstableVault ? symbolDataDisplayProcessing(vaultTvl, '$') : apiVaultInfo?.vaultsTvlDisplay) || '-'}
              valueStyle={{
                color: 'text_caption',
                h: '20px',
                lineHeight: '20px',
                fontWeight: '500'
              }}
            />
            {isUnstableVault && (
              <VTextLabelBox
                wrapStyle={{
                  alignItems: 'flex-end',
                  gap: '2px'
                }}
                title={
                  <HStack>
                    <Text fontSize="12px">Performance Fee</Text>
                    <TooltipIcon type="vaultDetailFee" />
                  </HStack>
                }
                titleStyle={{
                  fontSize: '12px',
                  mt: '-4px'
                }}
                value={symbolDataDisplayProcessing(performanceFee, '%') || '-'}
                valueStyle={{
                  color: 'text_caption',
                  h: '20px',
                  lineHeight: '20px',
                  fontWeight: '500'
                }}
              />
            )}
          </>
        )}
      </Stack>
    </Stack>
  )
}
