import PriceRangeChart from '@/components/chart/PriceRangeChart'
import useGetPriceRangeData from '@/hooks/unstable-vault/useGetVaultPriceRange'
import { Block, SelectTab } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d } from '@cetus/utils'
import { Box, HStack, Spinner, Text } from '@chakra-ui/react'
import { useEffect, useState } from 'react'

export default function PriceRangeChartPageBlock({
  isRefresh,
  vaultsId,
  isReverse
}: {
  vaultsId?: string
  isRefresh?: boolean
  isReverse?: boolean
}) {
  const [isLoading, setIsLoading] = useState(false)
  const { getPriceRangeData } = useGetPriceRangeData()
  const [priceRangeData, setPriceRangeDate] = useState()

  const chartTypeList = [
    {
      label: '24H'
    },
    {
      label: '1W'
    }
  ]
  const [dateType, setDateType] = useState<'24H' | '1W'>('1W')
  const [isTabLoading, setIsTabLoading] = useState(false)

  const fetchPricerangeHistogram = async (type: string) => {
    const now = Date.now()
    const oneDay = 86400 // 1 天的秒数
    const beginTimestamp = String(Math.floor(now / 1000 - oneDay))
    try {
      setIsLoading(true)
      const res = await getPriceRangeData({
        vaultID: vaultsId,
        dateType: type == '24H' ? 'min' : 'hour',
        beginTimestamp: type == '24H' ? beginTimestamp : '',
        endTimestamp: d(new Date().getTime()).toFixed(0)
      })

      const result = res.map((item: any) => {
        const lower = isReverse ? d(1).div(d(item.upper)).toString() : item.lower
        const upper = isReverse ? d(1).div(d(item.lower)).toString() : item.upper
        const real = isReverse ? d(1).div(item.real).toString() : item.real
        return {
          ...item,
          lower,
          upper,
          real
        }
      })

      setPriceRangeDate(result)
      setIsTabLoading(false)
      setIsLoading(false)
    } catch (error) {
      setIsLoading(false)
      setIsTabLoading(false)
      console.log('🚀🚀🚀 ~ PriceRangeChartPageBlock.tsx:40 ~ fetchPricerangeHistogram ~ error:', error)
    }
  }

  useEffect(() => {
    if (vaultsId) {
      fetchPricerangeHistogram(dateType)
    }
  }, [vaultsId, dateType])

  useEffect(() => {
    if (isRefresh) {
      fetchPricerangeHistogram(dateType)
    }
  }, [isRefresh])

  const { isApp } = useWindowWidth()

  return (
    <Block bg="bg_six" borderColor="border_secondary" padding={isApp ? '12px' : '20px 8px 20px 20px'}>
      <HStack justify="space-between">
        <HStack pr="12px" w="100%" justify="space-between" gap="4px">
          <Text fontWeight="500" fontSize="14px">
            Historical Ranges
          </Text>
          <SelectTab
            type="outlineTab"
            tabList={chartTypeList}
            currentTab={dateType}
            handleChangeTab={tab => {
              setIsTabLoading(true)
              setDateType(tab.label)
            }}
            wrapStyle={{
              w: '88px',
              h: '32px',
              p: '3px',
              borderRadius: '8px'
            }}
            itemStyle={{
              flex: '1',
              fontSize: '14px',
              margin: '0px'
            }}
          />
        </HStack>
      </HStack>
      <Box w="100%" h="260px" mt="20px">
        <Box w="100%" h="260px" position="relative">
          {isLoading && (
            <Box position="absolute" top="50%" left="50%" transform="translate(-50%,-50%)">
              <Spinner />
            </Box>
          )}
          <PriceRangeChart data={priceRangeData} dateType={dateType} isTabLoading={isTabLoading} />
        </Box>
      </Box>
    </Block>
  )
}
