import { PositionSlider } from '@/components/position/details/RemoveBlock'
import vaultInterface from '@/hooks/vault-interface'
import { useCalculateChangeLpRate, useCalculateSlippageAmount } from '@/hooks/vaults/useVaultsHelper'
import useUnstableVaultsListStore from '@/store/unstable-vaults/useVaultsList'
import { AssetActionType, VaultApiInfo, VaultCoinAmounts } from '@/types/vaults'
import { Block, TradeInputGroup } from '@cetus/design'
import { useAccountStore } from '@cetus/stores/src/useAccountStore'
import { Token } from '@cetus/types'
import HTextLabelBox from '@cetus/ui-kit/src/components/HTextLabelBox'
import { formatCurrency, formatNumber, fromDecimalsAmountFix } from '@cetus/utils/src/formatter'
import { d } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { Box, Button, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import { SwitchAssetTab } from './SwitchAssetTab'

type VaultsRemoveProps = {
  apiVaultInfo?: VaultApiInfo
  tokenA?: Token
  tokenB?: Token
  isReverse: boolean
  vaultsId?: string
  totalLp?: string
  holdLpAmount?: string
  holdCoinAmounts: VaultCoinAmounts
  handleRefreshClick: () => void
  isUnstableVault: boolean
  currentActionTab?: string
  lpTokenInfo?: Token
}

export function VaultsRemove(props: VaultsRemoveProps) {
  const {
    apiVaultInfo,
    tokenA,
    tokenB,
    isReverse,
    vaultsId,
    totalLp,
    holdCoinAmounts,
    holdLpAmount,
    handleRefreshClick,
    isUnstableVault,
    currentActionTab,
    lpTokenInfo
  } = props
  const useVaultsRemove = vaultInterface.useVaultsRemove(isUnstableVault)
  const useAddButtonStatus = vaultInterface.useAddButtonStatus(isUnstableVault)
  const [isCheckedZAP, setIsCheckedZAP] = useState(false)
  const [assetAction, setAssetAction] = useState<AssetActionType>('both')
  const { currentAccount, onWalletModal } = useAccountStore()

  const { contractClmmPoolObj } = useUnstableVaultsListStore()

  const contractClmmPoolInfo: any = useMemo(() => {
    if (contractClmmPoolObj && contractClmmPoolObj[apiVaultInfo?.clmmPoolAddress]) {
      return contractClmmPoolObj[apiVaultInfo?.clmmPoolAddress]
    }
    return undefined
  }, [apiVaultInfo?.clmmPoolAddress, contractClmmPoolObj])
  const {
    amountInputA,
    amountInputB,
    availableAmountA,
    availableAmountB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    fixAmountA,
    inputTotalValue,
    handleAmountInputChange,
    calculateResult,
    showOnlySideTips,
    doRemoveAction,
    handlePercentInputChange,
    removePercent,
    isPercentInput,
    reCalculateResult,
    calculateAvailableLoading,
    submitLoading
  } = useVaultsRemove(
    assetAction,
    handleRefreshClick,
    holdCoinAmounts,
    holdLpAmount,
    vaultsId,
    isReverse ? tokenB : tokenA,
    isReverse ? tokenA : tokenB,
    isReverse,
    isCheckedZAP,
    contractClmmPoolInfo
  )

  const inputTotalValueError = useMemo(() => {
    if (inputTotalValue && isCheckedZAP) {
      return d(inputTotalValue).gt(0) && d(inputTotalValue).lt(0.0001)
    }
    return false
  }, [isCheckedZAP, inputTotalValue])

  const { btnText, btnDisabled } = useAddButtonStatus(
    isCheckedZAP,
    assetAction,
    false,
    amountInputA,
    amountInputB,
    isReverse ? !fixAmountA : fixAmountA,
    availableAmountA,
    availableAmountB,
    tokenA,
    tokenB,
    '',
    calculateResult,
    inputTotalValueError
  )
  // 占比
  const { lpRate } = useCalculateChangeLpRate(calculateResult?.burn_ft_amount, totalLp)
  // 最小可接收
  const { amountLimit } = useCalculateSlippageAmount(0, calculateResult?.burn_ft_amount, false)

  const balanceA = useMemo(() => {
    return availableAmountA
  }, [availableAmountA, isReverse])
  console.log('🚀🚀🚀 ~ VaultsRemove.tsx:85 ~ balanceA ~ availableAmountA:', availableAmountA)

  const balanceB = useMemo(() => {
    return availableAmountB
  }, [availableAmountB])
  console.log('🚀🚀🚀 ~ VaultsRemove.tsx:85 ~ balanceB ~ availableAmountB:', availableAmountB)

  const displayTokenA = useMemo(() => {
    return tokenA
  }, [isReverse, tokenA, tokenB])

  const displayTokenB = useMemo(() => {
    return tokenB
  }, [isReverse, tokenA, tokenB])

  const showTokenALoading = useMemo(() => {
    if (preCalculateLoading) {
      if (isPercentInput) {
        return true
      }
      if (isReverse) {
        return fixAmountA
      }
      return !fixAmountA
    }
    return false
  }, [preCalculateLoading, isPercentInput])

  const showTokenBLoading = useMemo(() => {
    if (preCalculateLoading) {
      if (isPercentInput) {
        return true
      }
      if (isReverse) {
        return !fixAmountA
      }
      return fixAmountA
    }
    return false
  }, [preCalculateLoading, isPercentInput])

  const LPBurnAmount = useMemo(() => {
    return formatNumber(fromDecimalsAmountFix(amountLimit || '0', lpTokenInfo?.decimals), lpTokenInfo?.decimals)
  }, [amountLimit, lpTokenInfo])

  return (
    <VStack gap="12px" w="100%" align="flex-start">
      <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p={{ base: '16px 8px 8px', lg: '16px' }} w="100%">
        <SwitchAssetTab
          label="Remove Amounts"
          displayTokenA={isReverse ? tokenA : tokenA}
          displayTokenB={isReverse ? tokenB : tokenB}
          onSelectAssetTab={setAssetAction}
          handleRefreshClick={() => {
            handleRefreshClick()
            reCalculateResult()
          }}
          isCheckedZAP={isCheckedZAP}
          setIsCheckedZAP={setIsCheckedZAP}
          assetAction={assetAction}
          setAssetAction={setAssetAction}
          currentActionTab={currentActionTab}
          isUnstableVault={isUnstableVault}
        />
        <Box w="100%" position="relative" mt="16px">
          <TradeInputGroup
            borderRadius="12px"
            from={{
              wrapStyle: {
                borderRadius: '12px',
                h: 'unset',
                p: { base: '20px 8px', lg: '20px 16px' }
              },
              calculateAvailableLoading,
              hideSelf: assetAction !== 'both' && assetAction !== displayTokenA?.coin_type,
              balance: availableAmountA || '',
              value: amountInputA,
              amountValue: amountValueA,
              loading: showTokenALoading,
              onChange: (value: string, isClickMax?: boolean, isHalfClickMax?: boolean) => {
                console.log('🚀🚀🚀 ~ VaultsRemove.tsx:179 ~ VaultsRemove ~ value:', value)
                handleAmountInputChange(value, !isReverse, isClickMax, isHalfClickMax)
              },
              needRemainBalance: false,
              selectable: false,
              placeholder: '0.0',
              token: displayTokenA,
              balanceLabel: 'Available'
            }}
            to={{
              wrapStyle: {
                borderRadius: '12px',
                h: 'unset',
                p: { base: '20px 8px', lg: '20px 16px' }
              },
              balanceLabel: 'Available',
              calculateAvailableLoading,
              needRemainBalance: false,
              hideSelf: assetAction !== 'both' && assetAction !== displayTokenB?.coin_type,
              balance: availableAmountB || '',
              value: amountInputB,
              amountValue: amountValueB,
              loading: showTokenBLoading,
              onChange: (value: string, isClickMax?: boolean, isHalfClickMax?: boolean) => {
                handleAmountInputChange(value, isReverse, isClickMax, isHalfClickMax)
              },
              selectable: false,
              placeholder: '0.0',
              token: displayTokenB
            }}
            iconParams={{
              xlinkHref: '#icon-icon_add',
              svgFill: 'text_caption',
              iconCursor: 'default'
            }}
            iconHover={false}
          />
          {/* {[
            fromTokenLock && (assetAction == displayTokenA?.coin_type || assetAction == 'both'),
            toTokenLock && (assetAction == displayTokenB?.coin_type || assetAction == 'both')
          ].filter(Boolean)?.length === 1 && (
            <LockInput
              position="absolute"
              top={assetAction == 'both' ? (fromTokenLock ? '0px' : '116px') : '0px'}
              h="108px"
              borderRadius="12px"
              left="0"
              text=""
            />
          )} */}
        </Box>
      </Block>

      <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p={{ base: '16px 8px', lg: '16px' }}>
        <Text color="primary_gray">Amount</Text>
        <PositionSlider
          percentage={removePercent}
          onChange={(value: string | number) => {
            !calculateAvailableLoading ? handlePercentInputChange(Number(value.toString().replace('%', ''))) : ''
          }}
        />
      </Block>

      <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p="0px">
        <VStack w="100%" gap="20px">
          <Button
            m="-1px"
            isLoading={preCalculateLoading || submitLoading}
            w="100%"
            borderRadius="12px"
            h="52px"
            fontSize="18px"
            fontWeight="500"
            disabled={btnDisabled || preCalculateLoading || submitLoading}
            onClick={() => {
              if (currentAccount) {
                doRemoveAction()
              } else {
                onWalletModal(true)
              }
            }}
          >
            {btnText || (isCheckedZAP ? 'Zap Out' : 'Withdraw')}
          </Button>

          {inputTotalValueError && (
            <VStack w="100%" pl="16px" pr="16px" pb="20px" alignItems="start">
              <Text
                color="primary_red"
                fontSize="12px"
                textAlign="left"
                w="100%"
                bg="primary_red_opacity.10"
                p="12px"
                borderRadius="8px"
                lineHeight="20px"
              >
                The input is too small. Zap mode is not available.
              </Text>
            </VStack>
          )}

          {/* 展示计算结果 */}
          {calculateResult && !inputTotalValueError && (
            <VStack w="100%" pb="20px" pl="16px" pr="16px" gap="0px">
              {assetAction !== 'both' && showOnlySideTips && (
                <Text color="text_paragraph" width="100%" fontSize="12px" lineHeight="1.8" pb="20px">
                  {showOnlySideTips}
                </Text>
              )}
              <VStack w="100%" gap="20px">
                <HTextLabelBox
                  label="Total Withdraw"
                  labelStyle={{
                    fontSize: '14px'
                  }}
                  value={formatCurrency(inputTotalValue, 2)}
                  valueStyle={{
                    fontSize: '14px'
                  }}
                  isLoading={preCalculateLoading}
                />
                <HTextLabelBox
                  label="LP Burn Amount"
                  value={`${LPBurnAmount} ${`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}`}
                  labelStyle={{
                    fontSize: '14px'
                  }}
                  valueStyle={{
                    fontSize: '14px'
                  }}
                  isLoading={preCalculateLoading}
                />
              </VStack>
            </VStack>
          )}
        </VStack>
      </Block>
    </VStack>
  )
}
