import { Block } from '@cetus/design'
import SingleTokenInfo from '@cetus/design/src/components/common/SingleTokenInfo'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { VTextLabelBox } from '@cetus/ui-kit'
import { formatCurrency, formatNumber, symbolDataDisplayProcessing, textEllipses } from '@cetus/utils'
import { HStack, Progress, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import { AumLimit } from './AumLimit'

export function VaultsComposition(props: {
  vaultCoinAmounts?: any
  vaultsCoinAValue?: string
  vaultsCoinBValue?: string
  displayTokenA?: Token
  displayTokenB?: Token
  vaultsCoinPercent?: any
  isLoading?: boolean
  isReverse?: boolean
  depositRatio?: string
  hardCapUSD?: string
  vaultTvl?: string
}) {
  const {
    vaultsCoinPercent,
    displayTokenB,
    displayTokenA,
    vaultCoinAmounts,
    vaultsCoinAValue,
    vaultsCoinBValue,
    isReverse,
    depositRatio,
    hardCapUSD,
    vaultTvl
  } = props

  const displayValueA = useMemo(() => {
    return isReverse ? vaultsCoinBValue : vaultsCoinAValue
  }, [isReverse, vaultsCoinAValue, vaultsCoinBValue])

  const displayValueB = useMemo(() => {
    return isReverse ? vaultsCoinAValue : vaultsCoinBValue
  }, [isReverse, vaultsCoinAValue, vaultsCoinBValue])

  const displayPercentA = useMemo(() => {
    return isReverse ? vaultsCoinPercent?.percentB : vaultsCoinPercent?.percentA
  }, [isReverse, vaultsCoinPercent])

  const displayPercentB = useMemo(() => {
    return isReverse ? vaultsCoinPercent?.percentA : vaultsCoinPercent?.percentB
  }, [isReverse, vaultsCoinPercent])
  const { isApp } = useWindowWidth()
  return (
    <Block bg="bg_six" borderColor="border_secondary" p={{ base: '16px 8px', lg: '20px 16px' }}>
      <VStack w="100%" gap="0px">
        <HStack w="100%" justifyContent="space-between">
          <Text w="100%" textAlign="start" fontSize="16px" color="text_caption">
            Vault Composition
          </Text>
          {hardCapUSD && (
            <AumLimit
              depositRatio={depositRatio}
              hardCapUSD={hardCapUSD}
              vaultTvl={vaultTvl}
              label="Capacity"
              value={symbolDataDisplayProcessing(hardCapUSD, '$')}
            />
          )}
        </HStack>
        {/* 百分比  */}
        <HStack w={{ base: '100%', lg: '90%' }} justifyContent="space-between" mt="28px">
          <HStack>
            <SingleTokenInfo
              token={displayTokenA}
              haveName={false}
              haveSymbol={false}
              symbolFontSize="12px"
              warningIcon={{ iconW: '16px', iconH: '16px' }}
              imgBoxStyle={{ w: '32px', h: '32px' }}
              haveTooltip={!isApp}
            />
            <VTextLabelBox
              wrapStyle={{
                gap: '4px'
              }}
              title={textEllipses(displayTokenA?.symbol, 10) || ''}
              titleStyle={{
                fontSize: '16px',
                color: 'text_caption',
                fontWeight: '600'
              }}
              value={`${displayPercentA}%`}
              valueStyle={{
                fontSize: '12px',
                color: 'text_paragraph',
                fontWeight: '500'
              }}
            />
          </HStack>

          <Progress
            ml="16px"
            mr="16px"
            flex="1"
            h="4px"
            value={displayPercentA}
            bg={Number(displayPercentA) !== 0 && Number(displayPercentB) !== 0 ? 'quote_green' : 'process_bg_gray'}
            sx={{
              'div[role="progressbar"]': {
                bg: 'primary'
              }
            }}
          />
          <HStack>
            <VTextLabelBox
              wrapStyle={{
                gap: '4px',
                alignItems: 'flex-end'
              }}
              title={textEllipses(displayTokenB?.symbol, 10) || ''}
              titleStyle={{
                fontSize: '16px',
                color: 'text_caption',
                fontWeight: '600'
              }}
              value={`${displayPercentB}%`}
              valueStyle={{
                fontSize: '12px',
                color: 'text_paragraph',
                fontWeight: '500'
              }}
            />
            <SingleTokenInfo
              token={displayTokenB}
              haveName={false}
              haveSymbol={false}
              symbolFontSize="12px"
              warningIcon={{ iconW: '16px', iconH: '16px' }}
              imgBoxStyle={{ w: '32px', h: '32px' }}
              haveTooltip={true}
            />
          </HStack>
        </HStack>
        {/* 数量/价值 */}
        <HStack w={{ base: '100%', lg: '90%' }} justifyContent="space-between" mt="12px">
          <VTextLabelBox
            wrapStyle={{
              gap: { base: '8px', lg: '4px' }
            }}
            title={`${formatNumber(vaultCoinAmounts?.displayCoinA, 6)} ${textEllipses(displayTokenA?.symbol, 10)}`}
            titleStyle={{
              fontSize: { base: '12px', lg: '14px' },
              color: 'text_caption',
              fontWeight: '500'
            }}
            value={formatCurrency(displayValueA, 2)}
            valueStyle={{
              color: 'text_paragraph',
              fontSize: { base: '12px', lg: '14px' },
              fontWeight: '500'
            }}
          />

          <VTextLabelBox
            wrapStyle={{
              gap: { base: '8px', lg: '4px' },
              alignItems: 'flex-end'
            }}
            title={`${formatNumber(vaultCoinAmounts?.displayCoinB, 6)} ${textEllipses(displayTokenB?.symbol, 10)}`}
            titleStyle={{
              fontSize: { base: '12px', lg: '14px' },
              color: 'text_caption',
              fontWeight: '500'
            }}
            value={formatCurrency(displayValueB, 2)}
            valueStyle={{
              color: 'text_paragraph',
              fontSize: { base: '12px', lg: '14px' },
              fontWeight: '500'
            }}
          />
        </HStack>
      </VStack>
    </Block>
  )
}
