import { d, Decimal, formatNumber } from '@cetus/utils'
import { Box, Center, Flex, HStack, Image, Skeleton, Spinner, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'
import Comma from './Comma'
import icon_currentprice from '/images/icon_currentprice@2x.png'

type PositionPriceRangeProps = {
  displayMaxPrice?: string
  displayMinPrice?: string
  displayPrice?: string
  isLoading?: boolean
}

export default function PositionPriceRange(props: PositionPriceRangeProps) {
  const { displayMaxPrice, displayMinPrice, displayPrice, isLoading } = props

  const [elementWidth, setElementWidth] = useState<number>(0)
  const elementRef = useRef<HTMLDivElement>(null)

  const positionPriceRangeLoading = useMemo(() => {
    if (displayMinPrice && displayMaxPrice && displayPrice) {
      return false
    }
    return true
  }, [displayMaxPrice, displayMinPrice, displayPrice])

  useEffect(() => {
    const getElementWidth = () => {
      if (elementRef.current) {
        setElementWidth(elementRef.current.offsetWidth)
      }
    }

    getElementWidth()

    const resizeObserver = new ResizeObserver(getElementWidth)

    if (elementRef.current) {
      resizeObserver.observe(elementRef.current)
    }

    return () => {
      if (elementRef.current) {
        resizeObserver.unobserve(elementRef.current)
      }
    }
  }, [])

  const trackDirection = useMemo(() => {
    if (displayMaxPrice === '∞') return 'center'
    if (displayPrice && displayMinPrice && displayMaxPrice) {
      if (d(displayPrice).greaterThanOrEqualTo(displayMaxPrice)) {
        return 'end'
      }
      if (d(displayMinPrice).greaterThanOrEqualTo(displayPrice)) {
        return 'start'
      }
    }
    return 'center'
  }, [displayPrice, displayMinPrice, displayMaxPrice])

  // const sliderML = useMemo(() => {
  //   if (displayMaxPrice === '∞') return '0px'
  //   if (displayPrice && displayMinPrice && displayMaxPrice) {
  //     if (d(displayPrice).lessThanOrEqualTo(displayMaxPrice) && d(displayPrice).greaterThanOrEqualTo(displayMinPrice)) {
  //       const centerPrice = d(displayMaxPrice).add(displayMinPrice).div(2)
  //       const isRight = d(displayPrice).gt(centerPrice)
  //       let relativePosition = d(0)
  //       if (isRight) {
  //         const range = d(centerPrice).sub(displayMinPrice)
  //         relativePosition = d(displayPrice).sub(centerPrice).div(range)
  //       } else {
  //         const range = d(displayMaxPrice).sub(centerPrice)
  //         relativePosition = d(displayPrice).sub(displayMinPrice).div(range)
  //       }
  //       if (isRight) {
  //         return `${d(elementWidth).mul(relativePosition).toFixed(0)}px`
  //       } else {
  //         return `-${d(elementWidth).mul(d(1).sub(relativePosition)).toFixed(0)}px`
  //       }
  //     }
  //   }
  //   return '0px'
  // }, [displayPrice, displayMinPrice, displayMaxPrice, elementWidth])

  const sliderML = useMemo(() => {
    if (displayMaxPrice === '∞') return '0px'
    if (displayPrice && displayMinPrice && displayMaxPrice) {
      if (d(displayPrice).lessThanOrEqualTo(displayMaxPrice) && d(displayPrice).greaterThanOrEqualTo(displayMinPrice)) {
        const centerPrice = d(displayMaxPrice).add(displayMinPrice).div(2)
        const isRight = d(displayPrice).gt(centerPrice)
        let relativePosition = d(0)
        if (isRight) {
          const range = d(centerPrice).sub(displayMinPrice)
          relativePosition = d(displayPrice).sub(centerPrice).div(range)
        } else {
          const range = d(displayMaxPrice).sub(centerPrice)
          relativePosition = d(displayPrice).sub(displayMinPrice).div(range)
        }
        const offset = d(123).div(2) // 考虑 current price 元素的宽度
        if (isRight) {
          return `${d(elementWidth).mul(relativePosition).sub(offset).toFixed(0)}px`
        } else {
          return `-${d(elementWidth).mul(d(1).sub(relativePosition)).sub(offset).toFixed(0)}px`
        }
      }
    }
    return '0px'
  }, [displayPrice, displayMinPrice, displayMaxPrice, elementWidth])

  return (
    <>
      {!positionPriceRangeLoading ? (
        <Flex w="90%" direction="column" alignItems={trackDirection}>
          <VStack
            w={{
              md: '100%',
              lg: 'max-content'
            }}
            justifyContent="center"
            mb="-22px"
            gap="8px"
            ml={sliderML}
          >
            {/* 当前价格 */}
            {/* bg="bg_secondary" */}
            <VStack p="6px 24px" borderRadius="8px" gap="4px">
              <Text fontSize="xs" h="14px">
                Current Price
              </Text>
              {isLoading ? (
                <Flex h="18px" align="center">
                  <Skeleton />
                </Flex>
              ) : (
                <HStack>
                  <Text fontSize="16px" color="text_highlight">
                    <Comma value={formatNumber(displayPrice, 6, true, Decimal.ROUND_DOWN)} />
                  </Text>
                </HStack>
              )}
            </VStack>
            {/* 滑块 */}
            {/* {trackDirection === 'center' && ( */}
            <VStack zIndex="100">
              <Box w="2px" h="98px" bg="#76C8FF" />
              <Image src={icon_currentprice} w="30px" h="30px" mt="-18px" />
            </VStack>
            {/* )} */}
            {/* {trackDirection !== 'center' && <Box h="25px" />} */}
          </VStack>

          <Center
            zIndex="99"
            mt="-48px"
            ref={elementRef}
            pl={{ base: '0', lg: '60px' }}
            pr={{ base: '0', lg: '60px' }}
            h="50px"
            w="100%"
            bg="linear-gradient( 180deg, rgba(22,22,22,0) 0%, #191919 100%)"
            borderBottom="1px solid"
            borderBottomColor="border"
          >
            <Center w="100%" h="48px" bg="linear-gradient( 180deg, rgba(22,22,22,0) 0%, #1A5780 100%)" />
          </Center>
          {/* 最小值 */}
          <HStack
            justifyContent="space-between"
            pl={{
              md: '0',
              lg: '14px'
            }}
            pr="2px"
            mt="8px"
            w={{ base: '100%', lg: '90%' }}
          >
            <VStack
              pl={{ base: '0px', lg: '16px' }}
              pr="16px"
              pt="5px"
              pb="5px"
              borderRadius="8px"
              // bg="bg_secondary"
              gap="6px"
              maxW={{
                md: '48%',
                lg: 'auto'
              }}
            >
              <Text fontSize="12px">Min Price</Text>
              {isLoading ? (
                <Flex h="18px" align="center">
                  <Skeleton />
                </Flex>
              ) : (
                <Text
                  fontSize="14px"
                  fontWeight="500"
                  color="text_caption"
                  maxW={{
                    md: '100%',
                    lg: 'auto'
                  }}
                >
                  <Comma value={formatNumber(displayMinPrice, 6, true, Decimal.ROUND_DOWN)} />
                </Text>
              )}
            </VStack>
            {/* 最大值 */}
            <VStack
              pl="16px"
              pr={{ base: '0px', lg: '24px' }}
              pt="5px"
              pb="5px"
              borderRadius="8px"
              // bg="bg_secondary"
              gap="6px"
              maxW={{
                md: '48%',
                lg: 'auto'
              }}
            >
              <Text fontSize="12px">Max Price</Text>
              {isLoading ? (
                <Flex h="18px" align="center">
                  <Skeleton />
                </Flex>
              ) : (
                <Text
                  fontSize="14px"
                  fontWeight="500"
                  color="text_caption"
                  maxW={{
                    md: '100%',
                    lg: 'auto'
                  }}
                >
                  <Comma value={formatNumber(displayMaxPrice, 6, true, Decimal.ROUND_DOWN)} />
                </Text>
              )}
            </VStack>
          </HStack>
        </Flex>
      ) : (
        <Flex h="202px" width="100%" align="center" position="relative">
          <Box position="absolute" top="50%" left="50%" transform="translate(-50%,-50%)">
            <Spinner />
          </Box>
        </Flex>
      )}
    </>
  )
}
